.Note-Shelf {
    width: stretch;
    padding: 0 1em;
}
ul, li {
    list-style: none;
}
.Note-Item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .8em 1em;
    cursor: pointer;
    transition: transform, background-color 100ms;
    background-color: inherit;
    border-radius: .4em;
    margin: .3em 0;
}
a {
    text-decoration: none;
}
.Note-Item:hover {
    transform: translateZ(10%);
    background-color: rgba(0, 0, 0, 0.05);
}
.Note-Title {
    margin-right: 1em;
    font-weight: 600;
    font-size: 1em;
    color: #42445a;
}
.Left-Section {
    display: flex;
}
.Description {
    color: grey;
    font-size: .9em;
    font-weight: 350;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 80vh;
}
.Note-List-Last-Area {
    width: stretch;
    padding: 1em;
    font-size: .7em;
    color: black;
}
.Item-End {
    font-size: small;
    color: #42445a;
}
@keyframes pop-in {
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}
@keyframes slide-in {
    from {
        transform:  translateX(50%);
    }
    to {
        transform: translateX(0);
    }
}