a {
	color: inherit;
}
.Note-Creator {
	color: rgba(0, 0, 0, 0.7);
}
.Note-Creator-Header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0.7em;
	box-shadow: 0 0 3px 3px rgba(0, 0, 0, .1);
}
.Header-Left-Nav,
.Nav-Option {
	display: flex;
	align-items: center;
	justify-content: center;
}
.Note-Creator-Main {
	padding: 1em;
}
.Editable-Content {
	padding: 1em 0;
}
.Header-Right-Nav {
	width: 5em;
	display: flex;
	align-items: center;
	justify-content: space-between;
}